.task{
    display: flex;
   
    height: 64px;
    color: white;

    align-items: center;
    justify-items: center;

    font-family: Josefin Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;

    border-bottom: #393A4B solid 1px;
    /* identical to box height */

    color: #C8CBE7;
    
}


.task:first-of-type{
 
}

.task label{
    cursor: pointer;
    display: flex;
}

.task input[type=checkbox] {
   cursor: pointer;
   opacity: 0;
   position: absolute;
}

.task label::before{
    
    content: '';
    width: 24px;
    height: 24px;
   
    margin-left: .5em;
    margin-right: .5em;
    border: 0.1px solid #393A4B;
    border-radius: 50%;

}

.task input[type=checkbox]:checked+label::before
 {
    content: "\002714";
    background:linear-gradient(135deg, #55DDFF 0%, #C058F3 100%);
 
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
}



